@charset "UTF-8";

@import "../utils/variables/_colors";
@import "../utils/mixins/_buttons";
@import "../utils/mixins/_utilities";
@import "../utils/functions/_unit-conversion";

.stack {
  &__title {
    margin-bottom: 0;

    @media only screen and (min-width: 40em) {
      margin-bottom: rem(15);
    }
  }

  &__remove {
    @include buttonToLink;
    margin-bottom: rem(20);

    @media only screen and (min-width: 40em) {
      margin-bottom: rem(15);
      margin-top: rem(7);
      float: right;
    }
  }
}

.review {
  @include layout-list;
  border-top: solid 5px $govuk-blue;
  margin-bottom: rem(30);

  ul {
    list-style: disc;
  }

  > li {
    border-bottom: solid 1px $border-color;
    margin: 0;
    padding: rem(15) 0;
  }

  &__label {
    @include text-size-19;
    font-weight: $normal;
    margin-top: 0;
    margin-bottom: 5px;
  }

  &__value {
    font-weight: $bold;
    margin-bottom: 0;
  }

  &__change {
    @include buttonToLink;
    float: right;
  }

  &__row {
    @include float-row;
  }

  &__content {
    @include float-column(3/4);
    padding: 0;

    *:last-child {
      margin-bottom: 0;
    }
  }

  &__controls {
    @include float-column(1/4);
    padding: 0;
  }
}



