@charset "UTF-8";
@import "../functions/_unit-conversion";

////
/// @group Layout
////
$gutter: 30 !default;
/// Clearfix mixin
/// @link: http://cssmojo.com/the-very-latest-clearfix-reloaded/
@mixin clearfix {
  &::after {
    content: "";
    display: block;
    clear: both;
  }
}

/// Container (Float grid)
/// @param {number} width - the width of the container
/// @breakpoint {number} breakpoint - the point at which the gutters double
///
@mixin float-container($max-width, $breakpoint: 40em) {
  @include clearfix;
  max-width: rem($max-width);
  margin: 0 auto;
  padding-left: rem($gutter/2);
  padding-right: rem($gutter/2);
  @media only screen and (min-width: $breakpoint) {
    padding-left: rem($gutter);
    padding-right: rem($gutter);
  }
}

/// Row (Float grid)
@mixin float-row($padding-width: $gutter) {
  @include clearfix;
  margin-left: rem($padding-width * -0.5);
  margin-right: rem($padding-width * -0.5);
}

/// Column (Float grid)
/// @param {number} width - the width of the column
/// @param {string} $direction [left] - sets the direction of the float
/// @require {mixin} clearfix
/// @require {function} rem
///
/// @example scss - Columns mixin
///   .col-1-2 {
///     @include float-column(1/2, right);
///   }
///
/// @example css {
///   .col-1-2 {
///     content: "";
///     display: block;
///     clear: both;
///     float: right
///     padding: 2rem;
///     width: 50%;
///   }
///
@mixin float-column($width: 1, $direction: left) {
  float: $direction;
  padding-left: rem($gutter/2);
  padding-right: rem($gutter/2);
  width: $width * 100%;
}



