@charset "UTF-8";

@import "../utils/functions/_unit-conversion";
@import "../utils/mixins/_text-sizes";
@import "../utils/variables/_typography";
@import "../utils/variables/_colors";

.error {
  color: $red;
  font-weight: $bold;
  margin-bottom: rem(15);
}

.error--meta {
  color: $black;
  font-weight: normal;
  display: block;
  float: left;
  clear: left;

  @media screen and (min-width: 500px) {
    display: inline;
    float: none;
    clear: none;
    margin-left: rem(30);
  }
}

ul.error {
  list-style: disc;
}

legend .error {
  margin-top: rem(5);
}

.error-notification {
  background: $red;
  display: inline-block;
  padding: rem(15) rem(15) rem(15) rem(15);
  margin-bottom: rem(15);
  color: $white;
  font-weight: $bold;
  @include text-size-21;
}



