@charset "UTF-8";
@import "../variables/_colors";
@import "../variables/_typography";
@import "../functions/_unit-conversion";

@mixin notification($background-color) {
  background: $background-color;
  display: inline-block;
  padding: rem(15) rem(15) rem(0) rem(15);
  margin-bottom: rem(15);
  color: $white;
  font-weight: $bold;
  @include text-size-21;
}



