@charset "UTF-8";
@import "../utils/variables/_colors";
@import "../utils/variables/_typography";
@import "../utils/functions/_unit-conversion";
@import "../utils/mixins/_text-sizes";
@import "../utils/mixins/_layout";
@import "../utils/mixins/_utilities";

.sfe {
  body,
  button,
  input,
  select,
  textarea {
    font-family: nta, Arial, sans-serif;
  }
}

.sfw,
.sfni {
  body,
  button,
  input,
  select,
  textarea {
    font-family: Arial, sans-serif;
  }
}

body {
  background: $white;
  color: $black;
  @include text-size-19;

  > header:focus {
    outline: none;
  }
}

// Header
.page-header {
  background: $black;
  color: $white;
  padding-top: rem(15);
  padding-bottom: rem(12);
  margin-bottom: rem(30);

  &__inner {
    position: relative;
  }

  &__title {
    @include float-column;
    margin: 0;
  }

  &__content {
    @include float-column;
  }
}

// Header - GOV.UK
.sfe {
  .page-header {
    &__title {
      @extend .desktop-1-3;

      &-content {
        font-size: rem(30);
        font-weight: bold;
        line-height: rem(30);
        background-image: url('../images/template/govuk-crown.svg');
        background-size: rem(35) rem(31);
        background-repeat: no-repeat;
        background-position: rem(5) rem(3);
        padding-left: rem(48);
        text-rendering: optimizeLegibility;
        color: $white;
        text-decoration: none;
        border-bottom: 1px solid transparent;

        &:hover {
          border-bottom-color: $white;
        }

        &:focus {
          background-color: $black;
          outline: 3px solid $yellow;
        }
      }
    }

    &__content {
      @extend .desktop-2-3;
      @media (min-width: 40em) {
        @include text-size-24;
        font-weight: $bold;
        padding-top: rem(3);
        margin: 0;
        clear: none;
        line-height: rem(24);
      }

      @media (max-width: 40em) {
        @include visually-hidden;
      }
    }
  }
}

// Wales
.sfw {
  .page-header {
    &__title {
      @extend .tablet-1-2;
      font-size: rem(24);
    }

    &__content {
      @media (min-width: 40em) {
        width: percentage(1/2);
        text-align: right;
      }
    }
  }
}

// Northern Ireland
.sfni {
  .page-header {
    background: $sfni-blue;
    color: $white;
    padding: 0;

    .container {
      padding-top: rem(20);
      padding-bottom: rem(20);

      @media (min-width: 48em) {
        background: url('../images/template/sfni-header.svg') top right no-repeat;
        padding-top: rem(30);
        padding-bottom: rem(30);
      }
    }

    &__title {
      font-size: rem(24);
      line-height: 0;

      img {
        width: 100%;
        max-width: rem(300);
      }
    }
  }

  .page-footer {
    background: $sfni-blue;
    color: $white;
    padding: 0;

    .container {
      padding-top: rem(15);
      padding-bottom: rem(15);

      @media (min-width: 40em) {
        background: url('../images/template/sfni-footer.svg') bottom right no-repeat;
        padding-top: rem(30);
        padding-bottom: rem(30);
      }
    }

    a {
      color: $white;

      &:hover {
        color: $yellow;
      }
    }
  }
}

// Language Toggle
.language-toggle__link {
  color: $white;

  &:hover {
    color: lighten($sfw-pink, 20%);
  }

  &:focus {
    color: $black;
  }
}

// Feedback link
.feedback {
  font-size: rem(16);
  padding-top: rem(10);
  margin-top: rem(-30);
  margin-bottom: rem(30);

  p {
    margin: 0;
    padding-bottom: rem(8);
    display: table;
    border-bottom: 1px solid $border-color;
    width: 100%;
  }

  &__text {
    display: table-cell;
  }
}

.sfe .feedback {
  border-top: 10px solid $govuk-blue;
  margin-bottom: rem(30);
}

.top-divider {
  border-top: 10px solid $govuk-blue;
  padding-top: rem(10);
  margin-top: rem(-30);
  margin-bottom: rem(30);
}

.phase-tag {
  display: inline-block;
  vertical-align: top;

  @media (min-width: 40em) {
    vertical-align: baseline;
  }

  margin: 0 rem(8) rem(2) 0;
  padding: rem(2) rem(5) 0 rem(5);
  line-height: 1.5;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-decoration: none;
  color: #fff;
  background-color: $govuk-blue;
  width: auto;
}

// Footer
.page-footer {
  padding: rem(30) 0 rem(30) 0;
  font-size: rem(16);
  margin-top: rem(30);
  @include clearfix;

  a {
    color: $footer-color;

    &:hover {
      color: $black;
    }
  }

  &__inner {
    font-size: 0;
  }

  @media (min-width: 40em) {
    margin-top: rem(45);
  }

  &__service {
    @include text-size-16;
    margin-top: rem(30);

    @media (min-width: 40em) {
      margin-top: rem(15);
      float: left;
      width: 25%;
    }
  }
}

// Crown Copyright
.crown-copyright {
  margin: 0 auto;
  width: 100%;
  display: block;
  text-align: center;
  background: url('../images/template/govuk-crest.png') no-repeat 50% 0;
  height: rem(60);
  padding-top: rem(115);
  padding-bottom: rem(30);

  &:focus {
    background: $focus-color url('../images/template/govuk-crest.png') no-repeat 50% 0;
  }

  &:hover {
    opacity: 0.75;
  }

  @media (min-width: 40em) {
    background: url('../images/template/govuk-crest.png') no-repeat 100% 0;
    text-align: right;

    &:focus {
      background: $focus-color url('../images/template/govuk-crest.png') no-repeat 100% 0;
    }
  }
}

// Welsh Government
.welsh-government {
  float: right;
}

.sfe .page-footer {
  color: $footer-color;
  background: $panel-color;
}

.sfw .page-footer {
  background: $black;
  color: $white;

  a {
    color: $white;

    &:hover {
      color: lighten($sfw-pink, 20%);
    }

    &:focus {
      color: $black;
    }
  }
}

.footer-navigation {
  font-size: 16px;
  margin-top: rem(15);

  @media (min-width: 40em) {
    float: left;
    width: 75%;
  }

  ul {
    padding-left: 0;
  }

  li {
    list-style: none;
    display: inline;
    margin-right: rem(15);
  }
}

// SFE Logo
.secondary-logo {
  margin-bottom: rem(15);
  float: left;
  width: 100%;
  max-width: rem(322);

  @media only screen and (min-width: 60em) {
    margin-bottom: rem(30);
  }
}

// Sticky Footer
html,
body {
  height: 100%;
}

body {
  display: table;
  width: 100%;
}

.page-content {
  display: table-row;
  height: 100%;
}

// Browser not supported
.browser-support {
  background: $govuk-blue;
  padding: rem(30) 0;

  &__title {
    @include text-size-24;
    margin: 0;
    color: $white;
  }

  &__message {
    color: $white;
    margin: 0;
  }

  a {
    color: $white;

    &:hover {
      color: $yellow;
    }

    &:focus {
      color: $black;
    }
  }
}

.hidden {
  display: none;
}

// Sidebar
.sidebar {
  border-top: rem(10) solid $govuk-blue;
  padding-top: rem(15);

  &__item {
    margin-bottom: rem(30);
  }
}

.question {
  margin-bottom: rem(30);

  &:last-of-type {
    margin-bottom: 0;
  }
}



