@charset "UTF-8";

// Normalize.css
// See https://necolas.github.io/normalize.css/
@import "../vendors/_normalize";

// Base elements
html {
  font-size: 100%;
}

body {
  line-height: 1.5;
  margin: 0;
}

// Remove fieldset styling
fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

legend {
  line-height: 1.5;
}

// Remove default form input styling on Safari
input,
textarea {
  -webkit-appearance: none;
  border-radius: 0;
}



