@charset "UTF-8";

@import "../utils/variables/_colors";
@import "../utils/mixins/_buttons";
@import "../utils/mixins/_utilities";
@import "../utils/functions/_unit-conversion";

// Links
a {
  color: $link-color;

  &:hover {
    color: $link-hover-color;
  }

  &:focus {
    color: $link-focus-color;
    background: $focus-color;
    outline: 3px solid $focus-color;
  }

  &:active {
    color: $link-active-color;
  }
}

[rel="external"] {
  background: url('../images/icons/icon-external.svg') no-repeat 100% 30%;
  padding-right: rem(20);
  margin-right: rem(4);

  &:hover {
    background: url('../images/icons/icon-external.svg') no-repeat 100% 30%;
  }

  &:focus {
    background: $focus-color url('../images/icons/icon-external.svg') no-repeat 100% 30%;
  }
}

// Buttons
.sfe .button {
  @include button($button-color);
  padding: 0.5em 0.6em 0.4em 0.6em;
  margin-bottom: rem(15);

  &:focus {
    outline: 3px solid $focus-color;
    outline-offset: -1px;
  }
}

.sfw .button {
  @include button($sfw-pink);
  padding: 0.5em 0.6em 0.4em 0.6em;
  margin-bottom: rem(15);

  &:focus {
    outline: 3px solid $focus-color;
    outline-offset: -1px;
  }
}

.sfni .button {
  @include button($sfni-purple);
  padding: 0.5em 0.6em 0.4em 0.6em;
  margin-bottom: rem(15);

  &:focus {
    outline: 3px solid $focus-color;
    outline-offset: -1px;
  }
}

// Keyboard-only link
.keyboard-link {
  @include visually-hidden;

  &:focus {
    clear: left;
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    width: auto;
    background: $focus-color;
    color: $black;
    font-weight: 700;
    padding: rem(10);
    text-decoration: none;
    outline: 0;
    z-index: 1;
  }
}

// Links
.back-link {
  @include buttonToLink;
  display: -moz-inline-stack;
  display: block;
  position: relative;
  text-transform: none;
  line-height: 1;
  margin-bottom: rem(30);
  padding-left: 0.9em;
  text-decoration: underline;

  &::before {
    content: '';
    display: block;
    width: 0;
    height: 0;
    border-top: 0.4em solid transparent;
    border-right: 0.5em solid $link-color;
    border-bottom: 0.4em solid transparent;
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -0.4em;
  }

  &:hover,
  &:active {
    color: $link-hover-color;

    &::before {
      border-right: 9px solid $link-hover-color;
    }
  }
}

// Action groups
.action-group {
  @include clearfix;
  margin-bottom: rem(15);

  .action--primary,
  .action--secondary {
    float: left;
    clear: left;
  }

  *:last-child {
    margin-bottom: 0;
  }
}

.action--primary {
  @extend .button;
  margin-bottom: rem(15);
}

.action--secondary {
  @include buttonToLink;
  margin-bottom: rem(5);
}



