@charset "UTF-8";

@import "../utils/functions/_unit-conversion";

.numbered-area {
  @media only screen and (min-width: 60em) {
    margin-left: rem(100);
    position: relative;
  }

  &__number {
    padding-right: 0.5em;
    @media only screen and (min-width: 60em) {
      max-width: 35rem;
      position: absolute;
      font-size: rem(80);
      line-height: 1;
      top: rem(5);
      left: rem(-100);
    }
  }
}



