@charset "UTF-8";

.search-results {
  &__heading {
    margin-bottom: 0;
  }

  &__list {
    @include layout-list;
    margin-bottom: rem(30);
  }

  &__row {
    border-bottom: solid 1px $border-color;
    padding: rem(15) 0 rem(10) 0;

    &:only-child {
      border-bottom: none;
    }
  }

  &__link {
    @include buttonToLink;
    @include text-size-24;
    font-weight: bold;
    text-decoration: none;
    text-align: left;
  }

  &__meta {
    @include text-size-19;
    font-weight: normal;
    color: $black;
    margin-top: rem(5);
  }

  &__meta-item {
    margin-right: rem(15);
  }
}

.search-input {
  @extend .text-input--large;
  margin-right: rem(15);
  height: rem(45);
  float: left;

  @media (min-width: 40em) {
    margin-bottom: 0;
  }

  &--live {
    padding-right: rem(55);
  }

  &__icon {
    float: left;
    background: url('../images/icons/icon-search.svg') no-repeat center center $black;
    height: rem(45);
    width: rem(45);
    position: relative;
    left: rem(-60);
  }
}

.search-button {
  @extend .button;
}

.sfe,
.sfw,
.cmc {
  .search-button {
    margin-bottom: 0;
    line-height: 1.4;
    float: left;
    font-size: rem(19);
  }
}



