@charset "UTF-8";
@import "../variables/_colors";

// Button generator, based on GDS mixin
@mixin button($color: $button-color) {
  // Colour
  background-color: $color;

  // Size and shape
  position: relative;
  display: inline-block;
  padding: 0.3em 0.6em 0.2em 0.6em;
  border: none;
  border-radius: 0;
  -webkit-appearance: none;

  // Bottom edge effect
  box-shadow: 0 2px 0 darken($color, 15%);

  // Text
  font-size: 1em; // inherit from parent
  line-height: 1.25;
  text-decoration: none;
  -webkit-font-smoothing: antialiased;

  // Interaction
  cursor: pointer;

  &:visited {
    background-color: $color;
  }

  &:hover,
  &:focus {
    background-color: darken($color, 5%);
  }

  &:focus {
    outline: 3px solid $focus-color;
  }

  &:active {
    top: 2px;
    box-shadow: 0 0 0 $color;
  }

  // Disabled button styles
  &.disabled,
  &[disabled="disabled"],
  &[disabled] {
    opacity: 0.5;

    &:hover {
      cursor: default;
      background-color: $color;
    }

    &:active {
      top: 0;
      box-shadow: 0 2px 0 darken($color, 15%);
    }
  }

  // Set text colour depending on background colour
  @if lightness($color) < 50% {
    color: $white;

    &:link,
    &:hover,
    &:focus,
    &:visited {
      color: $white;
    }
  }

  @else {
    color: $text-color;

    &:link,
    &:hover,
    &:focus,
    &:visited {
      color: $text-color;
    }
  }

  // making the click target bigger than the button
  // (and fill the space made when the button moves)
  &::before {
    content: "";
    height: 110%;
    width: 100%;
    display: block;
    background: transparent;
    position: absolute;
    top: 0;
    left: 0;
  }

  &:active::before {
    top: -10%;
    height: 120%;
  }

  // Reset styles applied to external links
  &[rel="external"]::after {
    display: none;
    content: none;
    margin-left: 0;
    margin-right: 0;
  }
}

// Remove button styling, add pointer for link
@mixin buttonToLink {
  background: none;
  border: 0;
  padding: 0;
  color: $link-color;
  text-decoration: underline;
  cursor: pointer;

  &:focus {
    background: $focus-color;
    outline: 3px solid $focus-color;
  }
}



