@charset "UTF-8";

@import "../utils/functions/_unit-conversion";
@import "../utils/mixins/_text-sizes";
@import "../utils/mixins/_utilities";
@import "../utils/mixins/_buttons";
@import "../utils/variables/_colors";

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.inline {
  display: inline-block;
  margin-right: rem(15);
}

.label {
  display: block;
  margin-bottom: rem(10);
  font-weight: $bold;

  &__hint {
    @include text-size-19;
    font-weight: 400;
    color: $secondary-text-color;
  }
}

.date-field {
  &__label {
    font-weight: $normal;
    display: block;
    margin-bottom: rem(5);
  }

  &__legend {
    @extend .label;
    margin-bottom: rem(10);
  }

  &__example {
    color: $secondary-text-color;
    font-weight: 400;
    display: block;
    @include text-size-19;
  }

  &__day {
    width: 3em;
  }

  &__month {
    width: 3em;
  }

  &__year {
    width: 4em;
  }
}

.credits-field {
  width: 5em;
}

//IAG component
.information {
  background: $grey-3;
  position: relative;
  padding: rem(10);
  border-left: rem(5) solid $grey-1;

  &__text {
    font-weight: $normal;
    display: block;
  }
}

.text-input,
.email-input,
.number-input,
.postcode-input {
  display: block;
  border: 2px solid $black;
  padding: rem(10);
  margin-bottom: rem(15);

  &:focus {
    outline: 3px solid $focus-color;
    outline-offset: 0;
  }

  &--large {
    @extend .text-input;
    width: 100%;
    max-width: 25em;
  }

  &--xlarge {
    @extend .text-input;
    width: 100%;
  }

  &--inline {
    @extend .text-input;
    display: inline-block;
  }
}

.postcode-input {
  text-transform: uppercase;
  width: rem(200);
}

// Select
select {
  display: block;
  padding: 0.5em rem(60) 0.5em 0.5em;
  border: 2px solid $black;
  margin-bottom: rem(15);
  margin-right: rem(15);

  // Remove default arrows in Firefox, Chrome & Safari
  -webkit-appearance: none;
  -moz-appearance: none;

  // Create space for the arrow icon and add it
  background: url('../images/inputs/dropdown-arrow.svg') no-repeat right 10px center $white;

  &:focus {
    outline: 3px solid $focus-color;
  }

  &::-ms-expand {
    // Remove default arrow in IE
    display: none;
  }
}

option {
  outline: 0;
}

.select {
  &:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 #000;
  }
}

.radio {
  &__legend {
    @extend .label;
    margin-bottom: rem(20);
    max-width: 35em;
  }

  &__details {
    margin-top: rem(-10);
    margin-bottom: rem(20);
  }

  &__group {
    float: none;
    clear: left;
    position: relative;
    padding-left: rem(50);
    margin-bottom: rem(20);
    max-width: 35em;

    &:last-of-type {
      margin-bottom: rem(10);
    }

    &.is-inline {
      display: inline-block;
      margin-right: rem(30);
      margin-bottom: rem(5);
    }
  }

  &:last-child,
  &:last-of-type {
    margin-bottom: 0;
  }

  &--inline {
    clear: none;
    @media (min-width: 40em) {
      margin-bottom: 0;
      margin-right: rem(30);
    }
  }

  &__input {
    position: absolute;
    cursor: pointer;
    left: 0;
    top: 0;
    width: rem(38);
    height: rem(38);
    margin: 0;
    zoom: 1;
    opacity: 0;

    &:checked + label::after {
      zoom: 1;
      opacity: 1;
    }

    &:focus + label::before {
      box-shadow: 0 0 0 rem(5) #ffbf47;
    }
  }

  &__label {
    display: inline-block;
    cursor: pointer;
    margin-top: rem(6);

    &::before {
      content: "";
      border: rem(2) solid;
      background: transparent;
      width: rem(34);
      height: rem(34);
      position: absolute;
      top: 0;
      left: 0;
      border-radius: 50%;
    }

    &::after {
      content: "";
      border: rem(10) solid;
      width: 0;
      height: 0;
      position: absolute;
      top: rem(9);
      left: rem(9);
      border-radius: 50%;
      zoom: 1;
      opacity: 0;
    }
  }

  &__hint {
    color: $secondary-text-color;
  }
}

.checkbox {
  &__legend {
    @extend .label;
    margin-bottom: rem(15);
    max-width: 35em;
  }

  &__group {
    float: none;
    clear: left;
    position: relative;
    padding-left: rem(50);
    max-width: 35em;

    &:last-of-type label {
      margin-bottom: rem(10);
    }
  }

  &:last-child,
  &:last-of-type {
    margin-bottom: 0;
  }

  &--inline {
    clear: none;
    @media (min-width: 40em) {
      margin-bottom: 0;
      margin-right: rem(30);
    }
  }

  &__input {
    position: absolute;
    cursor: pointer;
    left: 0;
    top: 0;
    width: rem(38);
    height: rem(38);
    margin: 0;
    zoom: 1;
    opacity: 0;

    &:checked ~ label::after {
      zoom: 1;
      opacity: 1;
    }

    &:focus ~ label::before {
      box-shadow: 0 0 0 rem(5) #ffbf47;
    }
  }

  &__label {
    display: inline-block;
    cursor: pointer;
    min-height: rem(40);
    margin-bottom: rem(15);
    margin-top: rem(4);

    &.has-guidance {
      margin-top: rem(-4);

      &::before {
        top: 0;
      }

      &::after {
        top: rem(10);
      }
    }

    &::before {
      content: "";
      border: 2px solid;
      background: transparent;
      width: 34px;
      height: 34px;
      position: absolute;
      top: rem(0);
      left: 0;
    }

    &::after {
      content: "";
      border: solid;
      border-width: 0 0 5px 5px;
      background: transparent;
      width: 17px;
      height: 7px;
      position: absolute;
      top: rem(10);
      left: rem(8);
      transform: rotate(-45deg);
      zoom: 1;
      opacity: 0;
    }
  }

  &__hint {
    color: $secondary-text-color;
  }
}

// List input
.list-input {
  &__add {
    @include buttonToLink;
    @extend a;
    float: left;
    clear: left;
    line-height: 1;
    background: url("../images/icons/icon-add.svg") no-repeat;
    padding-left: rem(29); // 19px SVG + 10px visual padding

    &:hover {
      background-position: 0 rem(-19);
    }

    &:focus {
      height: 19px;
      background: url("../images/icons/icon-add.svg") $focus-color no-repeat 0 -38px;
    }
  }

  &__remove {
    margin-bottom: rem(30);
    float: left;
    clear: left;
    @include buttonToLink;
    @extend a;

    @media (min-width: 40em) {
      float: left;
      clear: none;
      margin-left: rem(10);
      position: relative;
      top: rem(11);
      margin-bottom: 0;
    }
  }

  &__field {
    @extend .text-input;
    float: left;
  }

  &__field--large {
    @extend .text-input--large;
    float: left;
    clear: left;
    margin-bottom: rem(10);

    @media (min-width: 40em) {
      margin-bottom: rem(30);
      clear: none;
    }
  }

  &__error {
    @extend .error;
    float: left;
    clear: left;
  }
}

// Guidance for radios and checkboxes
.input-guidance {
  @extend .indent;
  margin: rem(15) 0 0 0;
}

// Textarea
textarea {
  display: block;
  width: 100%;
  max-width: 35em;
  min-height: 8em;
  padding: rem(10);
  border: 2px solid $black;

  &:focus {
    outline: 3px solid $focus-color;
    outline-offset: 0;
  }
}

// Input groups
.form-group {
  @include clearfix;
  margin-bottom: rem(30);

  &--invalid {
    @include clearfix;
    @extend .form-group;
    border-left: rem(5) solid $red;
    padding-left: rem(15);

    .label {
      margin-bottom: 5px;
    }

    legend.label {
      position: relative;
      top: rem(-4);
      margin-bottom: 0;
    }
  }

  &__right {
    float: right;
  }
}

.currency-input {
  position: relative;

  &__denomination {
    float: left;
    padding-left: 0.6em;
    line-height: 44px;
    position: absolute;
    top: -1px;
    left: 1px;

    @media screen and (min-width: 40em) {
      line-height: 46px;
    }
  }

  &__field {
    padding-left: 1.75em;
  }
}

.date-range {
  &__from,
  &__to {
    @extend .column;
    @extend .tablet-1-2;
    max-width: rem(300);
  }
}



