@charset "UTF-8";

// For lists used for layout rather than text
@mixin layout-list {
  list-style: none;
  padding-left: 0;
}

// Visually Hidden
// For screen readers
@mixin visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

// Resets the bullet level of a unordered list
@mixin list-reset {
  list-style: disc;

  ul {
    list-style: circle;
  }
}



