@charset "UTF-8";
@import "../functions/_unit-conversion";

@mixin text-size-16 {
  font-size: rem(16);
}

@mixin text-size-19 {
  font-size: rem(16);
  @media (min-width: 40em) {
    font-size: rem(19);
  }
}

@mixin text-size-21 {
  font-size: rem(17);
  @media (min-width: 40em) {
    font-size: rem(21);
  }
}

@mixin text-size-24 {
  font-size: rem(18);
  @media (min-width: 40em) {
    font-size: rem(24);
  }
}

@mixin text-size-27 {
  font-size: rem(20);
  @media (min-width: 40em) {
    font-size: rem(27);
  }
}

@mixin text-size-36 {
  font-size: rem(24);
  @media (min-width: 40em) {
    font-size: rem(36);
  }
}

@mixin text-size-48 {
  font-size: em(32);
  @media (min-width: 40em) {
    font-size: rem(48);
  }
}



