/* stylelint-disable */
.modal {
  top: 20%;
  position: absolute;
  left: 0;
  right: 0;
  max-width: 90%;
  max-height: 90%;
  margin: auto;
  border: solid;
  border-image-source: initial;
  border-image-slice: initial;
  border-image-width: initial;
  border-image-outset: initial;
  border-image-repeat: initial;
  padding: 1em;
  background: white;
  color: black;

  @media (min-width: 40em) {
    max-width: 60%;
    max-height: 90%;
  }
}

.inactive-background {
  background: gray;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
}



