@charset "UTF-8";

// Breadcrumbs list
.breadcrumbs {
  padding-left: 0;
  margin: 0;
  list-style: none;
  line-height: 1.5em;
  color: $grey-1;

  li {
    display: inline;

    &::after {
      content: "\203A";
      padding: 0 em(2) 0 em(5);
      font-size: 1.75em;
      position: relative;
      top: em(2);
      color: $grey-2;
    }

    &:last-of-type {
      &::after {
        content: "";
      }
    }
  }
}



