@charset "UTF-8";

@import "../utils/variables/_colors";
@import "../utils/mixins/_buttons";
@import "../utils/mixins/_utilities";
@import "../utils/functions/_unit-conversion";

.dashboard-header {
  margin-bottom: rem(45);

  &__title {
    margin-bottom: rem(10);
  }

  &__subtitle {
    @extend .text--large;
    @extend .text--secondary;
  }
}

.section {
  margin-bottom: rem(60);
  border-bottom: solid 5px $govuk-blue;
  max-width: 40em;

  &__list {
    @include layout-list;
    border-top: solid 1px $grey-1;
    margin-bottom: 0;

    ul {
      list-style: disc;
    }

    > li {
      border-bottom: solid 1px $grey-1;
      margin: 0;
      padding: rem(30) 0;
    }
  }

  &__label {
    @include text-size-19;
    float: left;
    font-weight: $bold;
    margin: 0;

    @media (max-width: 20em) {
      margin-bottom: rem(10);
    }

    &--heading {
      @extend .heading--large;
    }
  }

  &__status_incomplete {
    @include text-size-16;
    font-weight: $bold;
    background-color: $mellow-red;
    color: white;
    padding: 3px 5px 1px 5px;
    float: left;
    clear: left;

    @media (min-width: 20em) {
      float: right;
      clear: none;
    }
  }

  &__status_completed {
    @include text-size-16;
    font-weight: $bold;
    background-color: $govuk-blue;
    color: white;
    padding: 3px 5px 1px 5px;
    float: left;
    clear: left;

    @media (min-width: 20em) {
      float: right;
      clear: none;
    }
  }

  &__row {
    @include float-row;
  }
}



