@charset "UTF-8";

th {
  text-align: left;
}

.table--data {
  width: 100%;
  text-align: left;
  margin-bottom: 15px;
  border-top: 4px solid $black;

  tr {
    border-bottom: 1px solid $border-color;
  }

  td,
  th {
    padding: rem(10);

    &:first-of-type {
      padding-left: 0;
    }

    &:last-of-type {
      padding-right: 0;
    }
  }

  @media (max-width: 30em) {
    font-size: rem(14);
  }

  @media (max-width: 40em) {
    border: 0;

    thead {
      display: none;
    }

    tr {
      margin-bottom: 10px;
      display: block;
      float: left;
      clear: left;
      width: 100%;
      border: 1px solid $border-color;
    }

    tr::after {
      content: "";
      display: table;
      clear: both;
    }

    tr::before {
      display: block;
      border-bottom: 2px solid red;
    }

    td {
      box-sizing: border-box;
      display: block;
      float: left;
      clear: left;
      width: 100%;
      text-align: right;
      border-bottom: 1px solid $border-color;

      &:first-of-type {
        padding: rem(10);
      }

      &:last-of-type {
        padding: rem(10);
      }
    }

    td:last-of-type {
      border-bottom: 0;
    }

    td::before {
      content: attr(data-label);
      float: left;
      font-weight: bold;
    }
  }
}

// Value tables with totals
.table--has-total {
  tr:last-of-type {
    font-weight: $bold;
  }

  @media (max-width: 40em) {
    tr:last-of-type {
      border: 0;

      td:last-of-type {
        padding-right: 0;
      }
    }

    th[scope="row"] {
      display: block;
      padding-bottom: 0;
      font-size: em(14);
      text-align: right;
    }
  }
}

// Table Cell Types
.table-cell--numeric {
  text-align: right;
}

.table {
  width: 100%;
  text-align: left;
  margin-bottom: 15px;
  border-collapse: collapse;

  td,
  th {
    padding: rem(10);
    border-bottom: 1px solid $border-color;

    &:first-of-type {
      padding-left: 0;
    }

    &:last-of-type {
      padding-right: 0;
    }
  }
}

.table-cell {
  border-bottom: 1px solid $border-color;
}

// Table Headings
.table-heading {
  margin-bottom: rem(10);
}

.table-subheading {
  margin-bottom: rem(5);
  font-weight: $normal;
  display: block;
  font-size: 0.75em;
  color: $secondary-text-color;

  @media (min-width: 60em) {
    color: $black;
    display: inline;
    margin-left: -0.15em;

    &::before {
      content: "\2009\2014\2009";
    }
  }
}

.finance {
  & table {
    border-collapse: collapse;
    border-spacing: 0;
    margin: 0 0 1em;
    padding: 0;
    width: 100%;
  }

  & th,
  & article td {
    vertical-align: top;
    line-height: 1.3em;
  }

  & tr:nth-child(even) td {
    background-color: #c1cacb;
  }

  & td {
    padding: 0.6em 0.5em;
  }

  & th {
    padding: 0.6em 0.5em;
    line-height: 1.25em;
    text-align: left;
    color: #fff;
    font-weight: normal;
    background-color: #0a3641;
    border: solid 2px #fff;
  }

  & td {
    background: #e1e8e8;
    border: solid 2px #fff;
  }

  & caption {
    text-align: left;
  }

  & caption {
    font-size: 1.25em;
    font-weight: bold;
    margin-bottom: 0.5em;
  }
}



