@charset "UTF-8";

@import "../utils/functions/_unit-conversion";
@import "../utils/mixins/_layout";
@import "../utils/mixins/_utilities";

// Use border-box model
* {
  box-sizing: border-box;
}

// Primary grid classes
.container {
  @include float-container(960px);
}

.column {
  @include float-column;
}

.row {
  @include float-row;
}

// Base Grid Sizes
.base-1-2,
.base-2-4 {
  width: percentage(1/2);
}

.base-1-3 {
  width: percentage(1/3);
}

.base-2-3 {
  width: percentage(2/3);
}

.base-1-4 {
  width: percentage(1/4);
}

.base-3-4 {
  width: percentage(3/4);
}

// Tablet (640px and up) grid sizes
@media (min-width: 40em) {
  .tablet-1-2 {
    width: percentage(1/2);
  }

  .tablet-1-3 {
    width: percentage(1/3);
  }

  .tablet-2-3 {
    width: percentage(2/3);
  }

  .tablet-1-2,
  .tablet-2-4 {
    width: percentage(1/2);
  }

  .tablet-1-4 {
    width: percentage(1/4);
  }

  .tablet-3-4 {
    width: percentage(3/4);
  }
}

// Desktop (960px and up) grid sizes
@media (min-width: 63.75em) {
  .desktop-1-2 {
    width: percentage(1/2);
  }

  .desktop-1-3 {
    width: percentage(1/3);
  }

  .desktop-2-3 {
    width: percentage(2/3);
  }

  .desktop-1-4 {
    width: percentage(1/4);
  }

  .desktop-3-4 {
    width: percentage(3/4);
  }
}

// Utilities
.clearfix {
  @include clearfix;
}

.clear {
  clear: left;
}

.list--layout {
  padding-left: 0;
  list-style: none;
}

// For lists used for layout rather than text
.list--layout {
  list-style: none;
  padding-left: 0;
}

// Visually Hidden
// Used for making text invisible text available to screen readers
.visually-hidden {
  @include visually-hidden;
}



