@charset "UTF-8";

@import "../utils/variables/_colors";
@import "../utils/mixins/_layout";
@import "../utils/mixins/_utilities";
@import "../utils/mixins/_notifications";
@import "../utils/functions/_unit-conversion";

// Content blocks
.content {
  max-width: 35em;
  margin-bottom: rem(30);
}

// Abbreviations
abbr[title] {
  text-decoration: none;
  cursor: help;
}

// Indented guidance
.indent {
  clear: both;
  border-left: rem(5) solid $border-color;
  padding-left: rem(15);

  &--thick {
    border-left: rem(10) solid $border-color;
    padding-left: rem(20);
  }
}

// Info
.info-notification {
  background: $govuk-blue;
  display: inline-block;
  font-weight: $normal;
  padding: rem(15) rem(15) rem(15) rem(15);
  margin-bottom: rem(15);
  color: $white;
  @include text-size-21;
}

// Warning icon
.warning {
  background: url("../images/icons/icon-warning.svg") no-repeat;
  padding-left: rem(44);
}

.success-notification {
  @include notification($turquoise);
}

// Lists
.list-reset {
  @include list-reset;
}

// Details
.details {
  margin-bottom: rem(15);
  @include clearfix;

  .arrow {
    margin-right: 0.35em;
    font-style: normal;
    text-decoration: none;
    color: $link-color;
  }

  summary {
    color: $link-color;

    &:hover {
      .arrow {
        color: $link-hover-color;
      }

      .details__summary {
        color: $link-hover-color;
      }
    }

    &:focus {
      outline: 3px solid $focus-color;
    }
  }

  &__summary {
    text-decoration: underline;
    color: $link-color;
    cursor: pointer;
    position: relative;
  }

  &__content {
    @extend .indent;
    max-width: 35em;
    position: relative;
    margin-top: rem(5);
    left: rem(3);
  }
}



