@charset "UTF-8";
@import "../utils/functions/_unit-conversion";

// Heading
.heading--xxlarge {
  @include text-size-48;
  display: block;
  margin-top: 0;
  margin-bottom: rem(15);
  font-weight: $bold;
}

.heading--xlarge {
  @include text-size-36;
  display: block;
  margin-top: 0;
  margin-bottom: rem(15);
  font-weight: $bold;
}

.heading--large {
  @include text-size-27;
  display: block;
  margin-top: 0;
  margin-bottom: rem(15);
  font-weight: $bold;
}

.heading--large {
  @include text-size-24;
  display: block;
  margin-top: 0;
  margin-bottom: rem(15);
  font-weight: $bold;
}

.heading {
  @include text-size-21;
  display: block;
  margin-top: 0;
  margin-bottom: rem(15);
  font-weight: $bold;
}

.heading--small {
  @include text-size-19;
  display: block;
  margin-top: 0;
  margin-bottom: rem(10);
  font-weight: $bold;
}

.heading--xsmall {
  font-size: rem(16);
  display: block;
  margin-top: 0;
  margin-bottom: rem(5);
  font-weight: $bold;
}

// Page Title
.page-title {
  margin-bottom: rem(30);
}

// Subtitles
.subheading {
  color: $secondary-text-color;
  font-weight: $normal;
  margin-top: rem(-15);
  margin-bottom: rem(15);
}

.subheading--above {
  color: $secondary-text-color;
  font-weight: $normal;
  margin-bottom: rem(-5);
}

// Text
.text--large {
  @include text-size-24;
  margin-bottom: rem(15);
}

.text {
  margin-bottom: rem(15);
}

.text--small {
  font-size: rem(16);
  margin-bottom: rem(15);
}

.text--secondary {
  color: $secondary-text-color;
}

ul,
ol {
  padding-left: 1em;
  margin-top: 0;
  margin-bottom: rem(10);
}

li {
  margin-bottom: rem(5);
}

p {
  margin-top: 0;
  margin-bottom: rem(15);
}

.lede {
  @include text-size-24;
}



